.product__container {
    background-image: url("../assets/bg_engine.jpg"); /* The image used */
    /* height: 500px; */
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: cover; /* Resize the background image to cover the entire container */
}

.product__container:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,.7);
    z-index: 2;
}

.product__flex__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 3;
    /* color: #fff; */
    /* text-align: center; */
    /* padding: 50px; */
}

.product__content {
    margin-top: 70px;
}




@media (max-width: 767px) {
    .product__flex__container {
        flex-direction: column;
        font-size: 0.8rem;
    }

    .product__flex__container .slider__image {
        margin-top: 50px;
    }

    .product__content {
        margin-top: 0px;
    }


    /* .slider__title {
        font-size: 0.7rem;
    } */
}

